/* === Ï†ïÎ†¨ Î≤ÑÌäº Î∞ïÏä§ === */
.btn-box {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin: 20px 0;
  flex-wrap: wrap;
}

.btn-box button {
  padding: 10px 20px;
  border-radius: 8px;
  font-weight: bold;
  transition: all 0.2s ease-in-out;
}

.btn-box button:hover {
  transform: scale(1.05);
  opacity: 0.9;
}

/* ÏÉÅÏÑ∏ÌéòÏù¥ÏßÄ Î†àÏù¥ÏïÑÏõÉ */
.detail-container { max-width: 1000px; margin: 40px auto; padding: 20px; }
.detail-card { display: flex; gap: 30px; background: #fff; border-radius: 15px; box-shadow: 0 4px 15px rgba(0,0,0,0.1); padding: 20px; align-items: center; }
.detail-image img { width: 100%; max-width: 400px; border-radius: 12px; object-fit: cover; }
.detail-info { flex: 1; text-align: left; }
.detail-info h2 { font-size: 2rem; margin-bottom: 15px; }
.detail-info .price { font-size: 1.3rem; font-weight: bold; margin-bottom: 15px; }

/* ÌÉ≠ */
.nav-tabs .nav-link { cursor: pointer; padding: 10px 20px; border-radius: 8px 8px 0 0; }
.nav-tabs .nav-link.active { font-weight: bold; color: #d9534f; }

/* ÌÉ≠ ÎÇ¥Ïö© */
.tab-content-box { border: 1px solid #eee; border-top: none; padding: 20px; border-radius: 0 0 10px 10px; background: #fafafa; margin-bottom: 40px; }

