.section-title {
  text-align: center;
  margin: 50px 0 30px;
}

.section-title h2 {
  position: relative;
  display: inline-block;
  font-size: 1.8rem;
  font-weight: 700;
  margin-bottom: 1rem;
  padding-bottom: 0.5rem;
}

.section-title h2::after {
  content: "";
  position: absolute;
  left: 53%;
  bottom: 0;
  transform: translateX(-50%);
  width: 100%;  /* Ï†úÎ™© Ìè≠Î≥¥Îã§ ÏÇ¥Ïßù Í∏∏Í≤å */
  height: 4px;
  background: linear-gradient(to right, #ff7e5f, #feb47b);
  border-radius: 2px;
}
