/* Ï†úÎ™© Î∞ïÏä§ */
.section-title-box {
  text-align: center;
  margin: 0px 5px 30px;
}

/* Í≥µÌÜµ Ï†úÎ™© Ïä§ÌÉÄÏùº */
.section-title {
  position: relative;
  display: inline-block;
  font-size: 1.8rem;
  font-weight: 700;
  margin-bottom: 1rem;
  padding-bottom: 0.5rem;
  color: #333;
}

/* Í∏∞Î≥∏ Î∞ëÏ§Ñ */
.section-title::after {
  content: "";
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
  width: 0;
  height: 4px;
  border-radius: 2px;
  transition: width 0.4s ease;
}
.section-title:hover::after {
  width: 100%;
}

/* üçé Í≥ºÏùº Ï†ÑÏö© (Î†àÎìú ‚Üí Ïò§Î†åÏßÄ) */
.fruit-title::after {
  background: linear-gradient(to right, #ff4e50, #f9d423);
}

/* ü•ï Ï±ÑÏÜå Ï†ÑÏö© (Í∑∏Î¶∞ ‚Üí ÏòêÎ°úÏö∞) */
.veggie-title::after {
  background: linear-gradient(to right, #56ab2f, #a8e063);
}

/* ===== ÏïÑÏù¥ÏΩò Ïï°ÏÖò ===== */
.icon-apple {
  display: inline-block;
  margin-right: 8px;
  transition: transform 0.3s ease;
  cursor: pointer;
}

/* Hover Ïãú Ï†êÌîÑ + ÌôïÎåÄ */
.icon-apple:hover {
  animation: appleJump 0.6s ease;
  transform: scale(1.2);
}

@keyframes appleJump {
  0%   { transform: translateY(0) scale(1.2); }
  30%  { transform: translateY(-12px) scale(1.3); }
  50%  { transform: translateY(0) scale(1.25); }
  70%  { transform: translateY(-6px) scale(1.2); }
  100% { transform: translateY(0) scale(1.2); }
}
