.detail-container {
  max-width: 900px;
  margin: 30px auto;
  padding: 20px;
}

.detail-card {
  display: flex;
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  padding: 20px;
}

.detail-image img {
  width: 300px;
  border-radius: 10px;
  margin-right: 20px;
}

.detail-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: left;
}

.detail-info h2 {
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 10px;
}

.detail-info .content {
  font-size: 15px;
  color: #666;
  margin-bottom: 15px;
  line-height: 1.5;
}

.detail-info .price {
  font-size: 22px;
  font-weight: bold;
  color: #e63946;
  margin-bottom: 20px;
}

.detail-info button {
  padding: 10px 20px;
  font-size: 16px;
  border-radius: 8px;
  transition: 0.3s;
}

.detail-info button:hover {
  background-color: #c82333;
  transform: scale(1.05);
}

.tab-content-box {
  margin-top: 20px;
  padding: 15px;
  background: #f8f9fa;
  border-radius: 8px;
  text-align: center;
}
